import React from 'react';

import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import {IUser} from "../../models/IUser";

type UserCardPropsType = {
    userInfo: IUser
}

export const UserCard = (props: UserCardPropsType) => {

    // const {data: pictureData} = picturesAPI.useFetchAllPicturesQuery(Math.floor(Math.random() * 100))

    return (
        <Card className={"mx-2 mb-5"} style={{width: '18rem'}}>
            <Card.Img className={"mt-2"} variant="top" src={props.userInfo.avatar}/>
            <Card.Body className={'d-flex flex-column justify-content-between'}>
                <div>
                    <Card.Title>
                        {props.userInfo.lastName + ' ' + props.userInfo.firstName}
                        <br/>
                        {props.userInfo.patronymic}
                    </Card.Title>
                    <Card.Text>
                        {props.userInfo.email}
                    </Card.Text>
                </div>
                <div className={'d-flex flex-row-reverse'}>
                    <Button variant="danger" className={"mt-3"}>Delete user</Button>
                </div>
            </Card.Body>
        </Card>
    );
};



