import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {IPicture} from "../models/IPictures";

export const picturesAPI = createApi({
    reducerPath: 'picturesAPI',
    baseQuery: fetchBaseQuery({baseUrl: 'https://api.thecatapi.com'}),
    endpoints: (build) => ({
        fetchAllPictures: build.query<IPicture[], number>({
            query: (limit: number = 10) => ({
                url: `v1/images/search`,
                params: {limit: limit}
            })
        })
    })
})